<template>
   <div>
     <md-toolbar class="md-primary">
      <div class="md-toolbar-row">
      <md-button class="md-icon-button" @click="menuVisible = true">
         <md-icon>menu</md-icon><md-tooltip md-direction="bottom" md-delay="300">Menù principale</md-tooltip>
      </md-button>
      <md-field md-clearable>
        <md-icon>search</md-icon><md-tooltip md-direction="bottom" md-delay="300">Cerca una misura</md-tooltip>
        <md-input placeholder="Ricerca misure" v-model=value @keyup.enter="submit(value)"></md-input>
      </md-field>
      <md-button class="md-icon-button" @click="userVisible = true">
         <md-icon>person</md-icon><md-tooltip md-direction="bottom" md-delay="300">Modifica il profilo utente</md-tooltip>
      </md-button>
      <md-button class="button-theme md-icon-button md-dense md-raised md-primary" @click="toggleTheme">
         <md-icon class="icon-github">invert_colors</md-icon><md-tooltip md-direction="bottom" md-delay="300">Cambia il tema</md-tooltip>
      </md-button>
      </div>
     </md-toolbar>

      <md-drawer :md-active.sync="menuVisible">
        <md-toolbar class="md-transparent" md-elevation="0">
          <span class="md-title">Azioni</span>
        </md-toolbar>
        <md-list>
          <md-list-item @click="setCurrentView('explore')">
            <md-icon>explore</md-icon>
            <span class="md-list-item-text">Esplora i bandi</span>
          </md-list-item>
          <md-list-item  @click="setCurrentView('about')">
            <md-icon>info</md-icon>
            <span class="md-list-item-text">Informazioni su AppMisure</span>
          </md-list-item>
          <md-list-item  @click="setCurrentView('ats_info')">
            <md-icon>home</md-icon>
            <span class="md-list-item-text">ATS</span>
          </md-list-item>
        </md-list>
      </md-drawer>
      <md-drawer class="md-right" :md-active.sync="userVisible" md-swipeable>
        <md-toolbar class="md-transparent" md-elevation="0">
          <div>
          <md-button class="md-title">Profilo Utente </md-button><md-tooltip md-direction="bottom" md-delay="300">I dati del profilo non vengono mai inviati al server</md-tooltip>
          </div>
          <div>
          <md-button class="md-icon-button" @click="resetProfile()"><md-icon>delete</md-icon></md-button><md-tooltip md-direction="bottom" md-delay="300">Cancella il profilo utente attuale</md-tooltip>
          </div>
        </md-toolbar>
        <md-content>
          <md-table>
            <md-table-row v-for="(item, index) in items()" :key="index" >
              <md-table-cell>{{item.name}}</md-table-cell>
              <md-table-cell><editable :id=index /></md-table-cell>
            </md-table-row>
          </md-table>
        </md-content>
      </md-drawer>
    </div>
</template>

<script>
import Vue from 'vue'
import VueMaterial from 'vue-material'
import Editable from './Editable'

Vue.use(VueMaterial)
Vue.use(Editable)

export default {
  name: 'AppMisureToolbar',
  components: {
    editable: Editable
  },
  data: () => ({
    menuVisible: false,
    userVisible: false,
    value: null
  }),
  methods: {
    items () {
      return this.$store.state.profile
    },
    resetProfile () {
      this.$store.commit('resetState')
    },
    setCurrentView (view) {
      this.$store.commit('setCurrentView', view)
    },
    submit (v) {
      console.log(v)
      this.$emit('search', v)
      this.setCurrentView('search_results')
    },
    toggleTheme () {
      this.$emit('toggleTheme')
    }
  }
}
</script>
