<template>
 <md-card v-show="check(value.requirements)">
   <md-card-header>
     <div class="md-title">{{value.name}}</div>
   </md-card-header>
   <md-card-content>
     {{value.text}}
   </md-card-content>
   <md-card-actions>
     <md-button @click="openExternal(value.apply_link)" class="md-raised">Applica</md-button>
     <md-button :href="'mailto:'+value.mailto_link " class="md-raised">Contatta</md-button>
   </md-card-actions>
 </md-card>
</template>

<script>
import Vue from 'vue'
import VueMaterial from 'vue-material'
Vue.use(VueMaterial)

export default {
  name: 'DisplayContent',
  props: ['value'],
  data: function () { return {} },
  methods: {
    openExternal: function (v) {
      window.open(v)
    },
    check: function (reqs) {
      for (var r in reqs) {
        const profile = this.$store.state.profile[reqs[r].name]
        const stringEval = profile.value + reqs[r].operator + reqs[r].value
        const success = global.eval(stringEval)
        console.log(stringEval, '=>', success)
        if (!success) { return false }
      }
      return true
    }
  }
}
</script>
