import Vue from 'vue'
import Vuex from 'vuex'
import App from './App.vue'
import './registerServiceWorker'
import createPersistedState from 'vuex-persistedstate'

const store = new Vuex.Store({
  state: {
    currentView: 'empty', // Values: empty, explore, about, ats_info, search_results
    profile: {
      isee: { value: 0, name: 'ISEE' },
      componenti_nucleo_familiare: { value: 1, name: 'Numero di componenti del nucleo familiare' },
      cap_residenza: { value: 20100, name: 'CAP del comune di residenza' }
    }
  },
  mutations: {
    resetState (state) {
      state.profile = {
        isee: { value: 0, name: 'ISEE' },
        componenti_nucleo_familiare: { value: 1, name: 'Numero di componenti del nucleo familiare' },
        cap_residenza: { value: 20100, name: 'CAP del comune di residenza' }
      }
    },
    setProfile (state, payload) {
      console.log(payload.field, payload.value)
      state.profile[payload.field].value = payload.value
    },
    setCurrentView (state, view) {
      state.currentView = view
    }
  },
  plugins: [createPersistedState()]
}
)

Vue.config.productionTip = false

new Vue({
  store,
  render: h => h(App)
}).$mount('#app')
