<template>
 <div>
   <span @click="editable = true"  v-if="!editable">{{value}}</span>
   <md-field  v-else>
    <md-input v-model.lazy="value" @keyup.esc="editable=false" @keyup.enter="editable=false">{{value}}</md-input>
   </md-field>
 </div>
</template>

<script>
import Vue from 'vue'
import VueMaterial from 'vue-material'

Vue.use(VueMaterial)

export default {
  name: 'Editable',
  props: ['id'],
  data: () => ({
    editable: false
  }),
  computed: {
    value: {
      get: function () { console.log(this.id, this.$store.state.profile[this.id].value); return this.$store.state.profile[this.id].value },
      set: function (v) { console.log(v); this.$store.commit('setProfile', { field: this.id, value: v }) }
    }
  }
}
</script>
